
#app {
  font-family: 'Fira Mono', monospace;
    text-align: left;
}
.container-fluid, .row {
  padding: inherit !important;
  margin: inherit !important;
}
/*for mobile device*/
@media screen and (max-width: 767px) {
.home, .projects, .team {
    min-height: 100vh;
}
h3 {
    margin-top: 20px;
}
p {
    font-size: 12px;
    padding: 0 7px ;
}
}
@media screen and (min-width: 768px) {
.home {
    min-height: 100vh;
}
p {
    font-size: 12px;
    padding: 0 3% ;
}
}


.home {
  background-color: #300922;
  color: #DBD5D5;
}
h1 {
  margin-top: 55px !important;
}
.btnLanguage {
  position: absolute;
  right: 5px !important;
  background-color: #DBD5D5;
  color: #330922;
}
@media screen and (min-width: 768px) {
h1 {
    margin-top: 25vh !important;
}
.home p {
    font-size: 16px !important;
}
}

.projects {
  /*min-height: 100vh;*/
  font-family: 'Fira Mono', monospace;
  color: #300922;
  text-align: left;
}
/*h3 {
    padding: 0 7px ;
}*/
@media screen and (min-width: 768px) {
p {
    font-size: 14px;
}
}


.team {
  color: #DBD5D5;
  margin: inherit !important;
}
.person {
  background-color: #300922;
  padding: inherit !important;
}
@media screen and (min-width: 768px) {
.person h4 {
    font-size: 14px;
    text-align: center;
    margin-top: 3px
}
.person p {
    font-size: 10px;
    margin: inherit !important;
}
}
.img {
  background-image: url(/static/img/background.3656d53.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.borderInput {
  width: 90%;
  border: inherit !important;
  border-bottom: 1px solid grey !important;
}
textarea{
  width: 90%;
  border: 2px solid grey;
}
